import { NavBar } from "./UI Components/NavBar/NavBar";
import { MainFooter } from "./UI Components/MainFooter/MainFooter";
import Button from "./UI Components/Button";
import Sheet from "./UI Components/Cards and Sections/Sheet/Sheet";
import Card from "./UI Components/Cards and Sections/Card/Card";
import { SKILLS, EXPERIENCES } from "./assets/resumeDetails";
import Star from "./UI Components/Star";
import LinkedInLogo from "./assets/linkedin-logo.png";

import "./app.scss";
import Accordian from "./UI Components/Accordian";

const App = () => (
  <>
    <NavBar hasLogo>
      <Button text="My Skills" isTextOnlyButton={true} />
      <Button text="My Experience" isTextOnlyButton={true} />
      <Button text="Contact Me" isTextOnlyButton={true} />
    </NavBar>
    <Sheet className="profile-section">
      <div className="profile-description">
        <h4>Hello, My Name is</h4>
        <h2>Siva Kumar Burle</h2>
        <h4>I am a Full Stack Developer</h4>
        <p>
          Having hands on experience in various
          technologies like ReactJS, Redux, Java and NodeJS. Experience in
          developing client-server applications using ReactJS, Java, HTML,
          JavaScript, CSS3. And also have knowledge on Micro Services
          Architecture, Micro Front End Architecture, .Net, knowledge of
          relational databases MS SQL Server, Oracle. A good team player and
          fast learner who adapts quickly with changing technology.
        </p>
      </div>
    </Sheet>
    <Sheet headerText="My Skills" className="skills">
      {SKILLS.slice(0, 6).map((skill) => (
        <Card headerIcon={skill.logo} headerText={skill.name}>
          <div className="expertise">
            <Star value={skill.expertise} />
            <span>{skill.expertise}</span>
          </div>
          <div className="experience">{skill.experience}</div>
        </Card>
      ))}
    </Sheet>
    <Sheet headerText="My Experience" className="experiences">
      {EXPERIENCES.map((experience) => (
        <Accordian 
        title={experience.organization_Name}
        content={experience.content}
        />
      ))}
    </Sheet>
    <MainFooter copyRightMessage={"Â© copyright Siva Kumar Burle"}>
      <a href="https://www.linkedin.com/in/siva-kumar-burle-09b2b8a4">
        <img src={LinkedInLogo} alt="LinkedIn Logo" />
      </a>
    </MainFooter>
  </>
);

export default App;
